ght: 1.8;\n}\n\n//Contextual colors\n\n.text-muted {\n  color: $text-muted !important;\n}\n\n// Letter spacings\n\n.ls-1 {\n  letter-spacing: 0.0625rem;\n}\n.ls-15 {\n  letter-spacing: 0.09375rem;\n}\n.ls-2 {\n  letter-spacing: 0.125rem;\n}\n","@include media-breakpoint-up(xl) {\n  .transform-perspective-right {\n    transform: scale(1) perspective(1040px) rotateY(-11deg) rotateX(2deg)\n      rotate(2deg);\n  }\n  .transform-perspective-left {\n    transform: scale(1) perspective(2000px) rotateY(11deg) rotateX(2deg)\n      rotate(-2deg);\n  }\n}\n",".alert {\n  border: 0;\n  color: $white;\n\n  .alert-link {\n    color: $white;\n  }\n\n  &.alert-success {\n    background-color: darken($success, 10%);\n  }\n\n  i.fa,\n  i.tim-icons {\n    font-size: $font-paragraph;\n  }\n\n  .close {\n    color: $white;\n    opacity: 0.9;\n    text-shadow: none;\n    line-height: 0;\n    outline: 0;\n  }\n\n  span[data-notify=\"icon\"] {\n    font-size: 22px;\n    display: block;\n    left: 19px;\n    position: absolute;\n    top: 50%;\n    margin-top: -11px;\n  }\n\n  button.close {\n    position: absolute;\n    right: 15px;\n    top: 50%;\n    margin-top: -13px;\n    width: 25px;\n    height: 25px;\n    padding: 3px;\n  }\n\n  .close ~ span {\n    display: block;\n    max-width: 89%;\n  }\n\n  &.alert-with-icon {\n    padding-left: 65px;\n  }\n}\n\n.alert-dismissible {\n  .close {\n    top: 50%;\n    right: $alert-padding-x;\n    padding: 0;\n    transform: translateY(-50%);\n    color: rgba($white, 0.6);\n    opacity: 1;\n\n    &:hover,\n    &:focus {\n      color: rgba($white, 0.9);\n      opacity: 1 !important;\n    }\n\n    @include media-breakpoint-down(xs) {\n      top: 1rem;\n      right: 0.5rem;\n    }\n\n    & > span:not(.sr-only) {\n      font-size: 1.5rem;\n      background-color: transparent;\n      color: rgba($white, 0.6);\n    }\n\n    &:hover,\n    &:focus {\n      & > span:not(.sr-only) {\n        background-color: transparent;\n        color: rgba($white, 0.9);\n      }\n    }\n  }\n}\n",".avatar {\n  color: $white;\n  background-color: $gray-500;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  font-size: $font-paragraph;\n  border-radius: 50%;\n  height: 48px;\n  width: 48px;\n}\n\n.avatar img {\n  width: 100%;\n  border-radius: 50%;\n}\n\n.avatar + .avatar-content {\n  display: inline-block;\n  margin-left: 0.75rem;\n}\n.avatar-lg {\n  width: 58px;\n  height: 58px;\n  font-size: $font-size-sm;\n}\n\n.avatar-sm {\n  width: 38px;\n  height: 38px;\n  font-size: $font-size-sm;\n}\n\n// Overlapped avatars\n\n.avatar-group {\n  .avatar {\n    position: relative;\n    z-index: 2;\n    border: 2px solid $card-bg;\n\n    &:hover {\n      z-index: 3;\n    }\n  }\n\n  .avatar + .avatar {\n    margin-left: -1rem;\n  }\n}\n","/*           badges             */\n.badge {\n  text-transform: uppercase;\n  line-height: 12px;\n  border: none;\n  text-decoration: none;\n  margin-bottom: 5px;\n\n  &:hover,\n  &:focus {\n    text-decoration: none;\n  }\n}\n\n.badge-icon {\n  padding: 0.4em 0.55em;\n\n  i {\n    font-size: 0.8em;\n  }\n}\n\n.badge-success {\n  @include badge-variant(darken($success, 10%));\n}\n",".btn,\n.navbar .navbar-nav > a.btn {\n  border-width: $border-thick;\n  border: none;\n  position: relative;\n  overflow: hidden;\n  margin: 4px 1px;\n  border-radius: $border-radius-lg;\n  cursor: pointer;\n\n  @include btn-styles($default, $default-states);\n\n  &:hover,\n  &:focus {\n    @include opacity(1);\n    outline: 0 !important;\n  }\n  &:active,\n  &.active,\n  .open > &.dropdown-toggle {\n    @include box-shadow(none);\n    outline: 0 !important;\n  }\n\n  .badge {\n    margin: 0;\n  }\n\n  &.btn-icon {\n    // see above for color variations\n    height: $icon-size-regular;\n    min-width: $icon-size-regular;\n    width: $icon-size-regular;\n    padding: 0;\n    font-size: $icon-font-size-regular;\n    overflow: hidden;\n    position: relative;\n    line-height: normal;\n\n    &.btn-simple {